
import {
  defineComponent, ref, computed, watch,
} from '@vue/composition-api';
import SearchAndSelect from '@/views/file-list/components/catalog/Search&Select.vue';
import Intersect from 'vue-intersect';
import { i18n } from '@/i18n';
import delay from '_/delay';

import { CatalogOptions } from '@/models/Catalog';
import { MaterialStatus } from '@/models/MaterialStatus';
import axios from '@/api/axios';
import tryParseJson from '_/try-parse-json';
import PriceForm from '@/views/file-list/components/catalog/PriceForm.vue';
import ImagePicker from '@/views/file-list/components/catalog/ImagePicker.vue';
import objectToFormData from '_/object-to-form-data';
import {
  SeriesNameToCode, SeasonNameToCode, SeasonKey, SeriesKey,
} from './ModelNameToCode';
import MaterialCard from './MaterialCard.vue';
// 获取可申请列表
const getSupplementMaterialList = async (data: object, groupAppId: string = '') => {
  return (await axios({
    method: 'POST',
    url: '/anta-material/material/supplement/search',
    data,
    headers: {
      groupAppId,
    },
  })).data;
};

// 提交申请
const addSupplementMaterialList = (
  materialIds: string[],
  supplementYear: number,
  supplementSeason: number,
  supplementSeries: string,
  groupAppId: string,
) => {
  return axios({
    method: 'POST',
    url: '/anta-material/material/supplement/add',
    data: {
      materialIds,
      supplementYear,
      supplementSeason,
      supplementSeries,
    },
    headers: {
      groupAppId,
    },
  });
};

type SearchParams = NonNullable<CatalogOptions['searchParams']>;

const rootSymbol = Symbol('root');

const AskForMaterial = defineComponent({
  name: 'AskForMaterial',
  props: {
    searchParams: {
      // type: Number,
      type: String,
    },
    workspaceId: {
      type: String,
      // required: true,
    },
    year: {
      type: String,
      reuqired: true,
    },
    season: {
      type: String as () => SeasonKey,
      reuqired: true,
    },
    series: {
      type: String as () => SeriesKey,
      reuqired: true,
      // default: '',
    },

  },
  setup(props, { root }) {
    if (!props.series || !Object.keys(SeriesNameToCode).includes(props.series)
      || !props.season || !Object.keys(SeasonNameToCode).includes(props.season)
      || !props.year || !/20\d{2}$/.test(props.year)) {
      root.$router.replace({ path: '/404' });
    }

    const currentPage = ref<number>(1);
    const pageSize = ref<number>(30);
    const materialList = ref([]);
    const pagingData = ref(null);

    const querySearchParams = ref<SearchParams>();
    watch(() => props.searchParams, (_searchParams) => {
      const s = tryParseJson(_searchParams || null);
      if (typeof s === 'object') {
        querySearchParams.value = s;
      } else {
        querySearchParams.value = undefined;
      }
    }, {
      immediate: true,
      flush: 'pre',
    });

    const isLoading = ref<boolean>(false);

    const fetchPagingList = async () => {
      isLoading.value = true;
      const _searchParams = querySearchParams.value;
      try {
        if (_searchParams && _searchParams.image) {
          const res = await axios({
            url: '/anta-material/material/management/mat/pic',
            method: 'post',
            data: objectToFormData({
              file: _searchParams.image,
            }),
          });

          pagingData.value = {
            total: res.data.length,
            pages: 1,
            current: 1,
            size: res.data.length,
            records: res.data.map(material => {
              const extraProperty = material.extraProperty ?? {};
              const suppliers = extraProperty.supplier_price_array ?? [];
              return {
                id: material.catalogId,
                relateId: material.id,
                fileInfo: {
                  ...material,
                  ...material.extraProperty,
                  ...material.imgJson,
                  supplier_price_obj: suppliers[0],
                  part_used: extraProperty['使用部位'],
                  material_specification: extraProperty['材料规格'],
                },
                inSupplement: false,
                relateType: 1,
                fileType: 1,
                name: material.name ?? material.materialName,
              };
            }),
          };
          materialList.value = pagingData.value.records
            .filter(item => item.fileInfo.material_status === MaterialStatus.DONE).map(item => ({ ...item, selected: false }));
        } else {
          pagingData.value = await getSupplementMaterialList({
            pageNumber: currentPage.value,
            pageSize: pageSize.value,
            supplementYear: Number(props.year),
            supplementSeason: SeasonNameToCode[props.season!],
            supplementSeries: SeriesNameToCode[props.series!],
            keyword: _searchParams?.keyword ?? '',
            antaAttachInfo: _searchParams?.filters
              ? Object.fromEntries(([
                'prices', 'productTypes', 'materialClassifications', 'years', 'seasons',
              ] as const).map(k => {
                const value = _searchParams.filters[k];
                console.info(value);
                return [
                  k,
                  // eslint-disable-next-line no-nested-ternary
                  value === undefined ? undefined : [value[value.length - 1]],
                ];
              }).filter(([, value]) => value !== undefined))
              : {},
          });

          materialList.value = [
            ...materialList.value,
            ...pagingData.value!.records.map((m) => ({ ...m, selected: false } as { [key: string]: any })),
          ];
        }
      } catch (e) {
        root.$errorHandler(e);
      } finally {
        isLoading.value = false;
      }
    };


    watch(() => {
      return {
        currentPage: currentPage.value,
        searchParams: props.searchParams,
      };
    }, ({ currentPage: _currentPage }) => {
      if (_currentPage === 1) {
        materialList.value = [];
      }
      fetchPagingList();
    }, {
      immediate: true,
    });

    const canLoadMore = computed(() => {
      if (!pagingData.value) {
        return false;
      }
      // if (pagingData.value.records.length < pagingData.value.size) {
      //   return false;
      // }
      return pagingData.value.pages > pagingData.value.current;
    });

    // 设置搜索项
    const setSearchItem = async (node: Tree, options, level: number) => {
      const productTypes = '品牌';
      if (!node) {
        const children = [{
          code: 'materialClassifications',
          value: 'materialClassifications',
          label: '材料分类',
          async getChildren(_, _level) {
            const _node = this;
            const params = {
              requestType: '3',
              ...(_level > 1 ? { parentId: _node.value } : {}),
            };
            return (await axios.post<
              {
                itemCode: string,
                itemValue: string,
                label: string,
                subCount: number | null,
              }[]
            >('bff-center/search/dict/conditions/detail', params, {
              headers: {
                language: i18n.language,
              },
            })).data.map(item => {
              return {
                value: item.itemValue,
                code: item.itemCode,
                label: item.label,
                leaf: !item.subCount,
              };
            });
          },
        }, {
          code: 'productTypes',
          value: 'productTypes',
          label: productTypes,
          async getChildren() {
            return (await axios.get(`anta-material/dynamics/property/value/title?title=${encodeURIComponent(productTypes)}`)).data
              .map((item: any) => {
                return {
                  code: item.id, value: item.id, label: item.label, leaf: true,
                };
              });
          },
        }, {
          code: 'years',
          value: 'years',
          label: '年份',
          children: Array.from(new Array(101)).map((_item, i) => {
            const year = `${i + 1970}`;
            return {
              code: year, value: year, label: year, leaf: true,
            };
          }),
        }, {
          code: 'seasons',
          value: 'seasons',
          label: '季节',
          children: Array.from(new Array(4)).map((_item, i) => {
            const q = `Q${i + 1}`;
            return {
              code: q, value: q, label: q, leaf: true,
            };
          }),
        }, {
          code: 'prices',
          value: { startPrice: undefined, endPrice: undefined, unit: 1 },
          isSelected(value) {
            return typeof value === 'object' && ('startPrice' in value || 'endPrice' in value) && 'unit' in value;
          },
          label: '价格区间',
          slot: PriceForm,
        }];
        return children.map(item => {
          return {
            ...item,
            leaf: false,
          };
        });
      }

      return node.children ?? [];
    };

    async function refresh(_params: { timestamp: number, searchParams?: SearchParams } = { timestamp: Date.now() }) {
      if ('searchParams' in _params) {
        querySearchParams.value = _params.searchParams;
        fetchPagingList();
      }
    }

    // 搜索
    const search = async (_searchParams: SearchParams) => {
      const m = '以图搜图不走$route';
      currentPage.value = 1;
      try {
        if (_searchParams && _searchParams.image) {
          throw new Error(m);
        }
        await root.$router.replace({
          query: {
            ...root.$route.query,
            // id: props.id,
            relateId: props.workspaceId,
            ...(_searchParams ? { searchParams: JSON.stringify(_searchParams) } : {}),
          },
        });
        return undefined;
      } catch (e) {
        // 路由相同时，刷新数据，用于以图搜图取消图片或者使用其他搜索的情况
        if (e.name === 'NavigationDuplicated' || e.message === m) {
          return refresh({ searchParams: _searchParams, timestamp: Date.now() });
        }
        throw e;
      }
    };

    // 已选面料
    const selectedIds = computed(() => {
      return materialList.value.filter(_m => _m.selected)?.map(__m => __m.id) || [];
      // return [];
    });

    async function addSupplementMaterial() {
      try {
        await addSupplementMaterialList(
          selectedIds.value,
          Number(props.year),
          SeasonNameToCode[props.season!],
          SeriesNameToCode[props.series!],
          props.workspaceId,
        );
        root.$message.success('您选择的面料已提交申请');
      } catch (e) {
        root.$errorHandler(e);
      }
      materialList.value = [];
      if (currentPage.value === 1) {
        fetchPagingList();
      } else {
        currentPage.value = 1;
      }

      await delay(2000);
      window.close();
    }

    function uploadImage(file) {
      if (file) {
        search({ filters: [], keyword: '', image: file });
      } else {
        search({ filters: [], keyword: '' });
      }
    }

    return {
      materialList,
      search,
      setSearchItem,
      selectedIds,
      canLoadMore,
      fetchPagingList,
      currentPage,
      addSupplementMaterial,
      querySearchParams,
      isLoading,
      uploadImage,
    };
  },

  render() {
    const {
      materialList, uploadImage,
      search, setSearchItem, selectedIds, workspaceId, canLoadMore, isLoading, addSupplementMaterial, querySearchParams,
    } = this as InstanceType<typeof AskForMaterial>;
    // jhfjd
    // <MaterialCard></MaterialCard>
    return <div style="width:100%" class="flex-column askfor-material">
      <section class="search-section">
        <div style="padding-bottom: 10px">申请材料</div>
        <SearchAndSelect
          style="width: 400px"
          searchSupplementMaterial={true}
          ref='searchbar'
          getChildren={setSearchItem}
          props={
            {
              ...(querySearchParams ? {
                value: querySearchParams,
              } : {}),
            }
          }
          onChange={search}
        >
          <el-popover slot="append-icon">
            <ImagePicker
              image={querySearchParams?.image}
              on={{
                'select-image': uploadImage,
              }}
            />
            <i
              slot="reference"
              class={['i-camera', { active: !!querySearchParams?.image }]}
            />
          </el-popover>
        </SearchAndSelect>
      </section>
      <section class="flex-column flex-1 scrollable-y">

        {
          materialList && (materialList.length
            ? <div class="flex-wrap flex-1" style={{ alignContent: 'baseline' }}>{
              materialList.map(m => {
                return <MaterialCard
                  material={m}
                />;
              })
            }</div>
            : <error error={{ code: 404, message: '暂无数据' }} style={{ flex: 1 }} />)
        }
        {
          // eslint-disable-next-line no-nested-ternary
          canLoadMore ? <Intersect onEnter={() => { this.currentPage += 1; }}>
            <div class="load-more">
              {
                isLoading ? [
                  <i class="el-icon-loading" />,
                  i18n.t('material-mall.loading'),
                ] : i18n.t('material-mall.scroll_to_load')
              }
            </div>
          </Intersect> : (materialList.length > 0 ? <el-divider>{i18n.t('material-mall.no_more')}</el-divider> : null)

        }
      </section>
      <section style="padding:24px 40px; background-color: #fff;">
        <el-button round class="float-r"
          style="margin-left: 8px;"
          type="primary"
          disabled={selectedIds.length === 0}
          onclick={() => addSupplementMaterial()}
        >确认申请</el-button>
        <el-button round class="float-r" onclick={() => { window.close(); }}>取消</el-button>
      </section>
    </div>;
  },
});

export default AskForMaterial;
