
import { computed, defineComponent } from '@vue/composition-api';

import AntaMaterialInfo from '@/views/material/info-anta/index.vue';

// import AntaMaterial from ''

const MaterialCard = defineComponent({
  name: 'MaterialCard',
  props: {
    material: {
      type: Object,
      required: true,
    },
  },
  setup(props, { root }) {
    const costPrice = computed(() => {
      const supplier = props.material.fileInfo.supplier_price_obj;
      return supplier?.cost_value ?? supplier?.reference_price_value;
    });

    async function handleShowMaterialDetail() {
      const { id } = props.material;

      const dialogResult = await root.$injectDialog({
        is: AntaMaterialInfo,
        props: {
          // groupAppId: props.workspaceId,
          id,
          editable: false,
        },
      }, {
        props: {
          // title: i18n.t('file-list.catalog.material_info'),
          // width: '703px',
          top: 'auto',
          bottom: 'auto',
        },
      });

      return dialogResult;
    }
    return {
      costPrice,
      handleShowMaterialDetail,
    };
  },

  render() {
    const {
      material, costPrice, $act, handleShowMaterialDetail,
    } = this;
    const 材料规格 = material.fileInfo.extraProperty?.材料规格 ?? material.fileInfo.material_specification;
    return <div class="material-card flex-column">
      <i
        class="btn-info iconfont icon-help"
        onclick={() => $act(handleShowMaterialDetail)}>
      </i>
      {
        material.inSupplement
          ? <span class="tip">申请中</span>
          : <el-checkbox vModel={material.selected} class="checkbox"></el-checkbox>

      }
      <base-image
        fit="contain"
        style="background-color: white"
        src={material.fileInfo.thumbnail}
        class="img">
      </base-image>
      <div
        class="info-container">
        <el-tooltip effect="dark" placement="bottom">
          <div slot="content">
            <div class="text-one-line">{material.name}</div>
            {材料规格 ? <div>{材料规格}</div> : null}
          </div>
          <div class="row">
            <span class="name text-one-line">{material.name}</span>
            {
              costPrice ? <small> ￥{costPrice}</small> : null
            }
          </div>
        </el-tooltip>
        {
          material.fileInfo.part_used
            ? <div class="row">
              <small class="">使用部位 <span class="text-one-line" title={material.fileInfo.part_used}>{material.fileInfo.part_used}</span></small>
            </div> : null
        }
      </div>
    </div>;
  },
});

export default MaterialCard;
